import { renderUserBoxs, renderUserBoxsSmScreens } from './data';


export default function Equipo() {
    return (
        <section className='py-10 bg-white relative'>

            <h1 className='font-bold text-[40px] text-[#3F2621] my-20 text-center'>Equipo</h1>

            <img src={require('./images/bg.png')} className='absolute w-full h-full object-cover right-0 top-0' />

            <div className='container flex flex-col items-center justify-center !mb-32'>

                <div className='hidden lg:block h-[600px] w-[700px] rounded-[200px] border border-[#45B8EA] relative'>

                    {renderUserBoxs}

                </div>

                <div className='lg:hidden flex items-center justify-center flex-col'>


                    {renderUserBoxsSmScreens}

                </div>

            </div>
        </section>
    )
}
