const userBox = [
    { name: "Diego Serrano", btnText: "doble espresso", describe: "Estrategia ycontrol ﬁnanciero Fundador Yebala Koffie", left: "-150px", top: "-50px" },
    { name: "Oumnia Chaara", btnText: "americano", describe: "Desarrollo tecnológico", left: "-150px", top: "50%", center: true },
    { name: "Clara Briones", btnText: "flat white", describe: "Branding", left: "-150px", bottom: "-50px" },
    { name: "Jesús Fortes", btnText: "cortado", describe: "Concepto y operaciones Fundador Yebala Koffie", right: "-150px", top: "-50px", },
    { name: "Jose Belda", btnText: "latte", describe: "Producto", right: "-150px", top: "50%", center: true },
    { name: "Carmen Jiménez", btnText: "carajillo", describe: "Comunicación", right: "-150px", bottom: "-50px" },
];

export const renderUserBoxs = userBox.map((user, index) => {
    return (
        <div key={index}

            className={`flex items-center w-[430px] absolute`}
            style={
                {
                    left: user.left ? user.left : 'initial',
                    right: user.right ? user.right : 'initial',
                    top: user.top ? user.top : 'initial',
                    bottom: user.bottom ? user.bottom : 'initial',
                    transform: user.center ? 'translateY(-50%)' : 'initial'
                }
            }
        >

            <div className='w-[100px]'>

                <img src={require(`./images/${index + 1}.png`)} alt='' className='min-w-[140px] relative z-10' />

            </div>

            <div className='border border-[#45B8EA] p-12 px-16 bg-[#FFF5EF] rounded-[100px] relative' style={{ width: 'calc(100% - 100px)' }}>

                <h2 className='text-[#45B8EA] mb-2 font-bold text-[20px]'>{user.name}</h2>

                <p className='font-[400] text-[13px]'>{user.describe}</p>

                <button className='px-10 py-4 hidden lg:block text-[13px] absolute bottom-[-20px] ms-[20px] w-fit text-white uppercase' style={{ fontFamily: "myFont", letterSpacing: "2px", background: `url(${require('./images/btn-bg.png')})`, backgroundRepeat: "no-repeat", backgroundSize: 'contain', objectFit: "fill", backgroundPosition: 'center' }}>{user.btnText}</button>

            </div>


        </div>
    )
})
export const renderUserBoxsSmScreens = userBox.map((user, index) => {
    return (
        <div key={index}
            className={`flex items-center w-full sm:w-[410px] mb-16`}
        >

            <div className='w-[60px]'>

                <img src={require(`./images/${index + 1}.png`)} alt='' className='min-w-[100px] relative z-10' />

            </div>

            <div className='flex-1 border border-[#45B8EA] p-12 px-16 sm:px-16 bg-[#FFF5EF] rounded-[100px] relative'>

                <h2 className='text-[#45B8EA] mb-2 font-bold text-[16px] sm:text-[20px]'>{user.name}</h2>

                <p className='font-medium text-[14px]'>{user.describe}</p>

                <button className='bg-[#45B8EA] absolute bottom-[-15px] ms-[20px] w-[180px] text-center text-white py-2 rounded-[25px] text-[13px] rotate-1 font-bold uppercase' style={{ fontFamily: 'myFont', letterSpacing: "2px", background: `url(${require('./images/btn-bg.png')})`, backgroundRepeat: "no-repeat", backgroundSize: 'contain', backgroundPosition: 'center' }}>{user.btnText}</button>

            </div>


        </div>
    )
})